import useStats from '../utils/useStats';
import Stats from './Stats';
import { useState } from 'react';

export default function CountrySelector() {
    const countries = useStats('https://covid19.mathdro.id/api/countries');

    const [selectedCountry, setSelectedCountry] = useState('USA');

    if(!countries) return <p>Loading...</p>;

    return (
        <div>
            <h2>Currently Showing {selectedCountry}</h2>
            <select onChange={(e) => {
                setSelectedCountry(e.target.value)
            } }>
                {Object.entries(countries.countries).map(([country, code]) => (
                    <option selected={selectedCountry === countries.iso3[code]} key={code} value={countries.iso3[code]}>
                        {country}
                    </option>
                 ) )}api
            </select>
            <Stats url={`https://covid19.mathdro.id/api/${selectedCountry}`}></Stats>
        </div>
    );
}